<template>
  <div v-if="tags" :class="['tag-list', 'grid-h', {active, multi}]">
    <d-period
      v-for="tag in tags"
      :key="tag"
      :tag="tag"
      @click.native="selectTag(tag)"
      :selected="selectedTags.includes(tag)"
      :needsAnS="needsAnS"
    ></d-period>
  </div>
</template>

<script>
import dPeriod from "@/components/dPeriod.vue";

export default {
  name: "d-tag-picker",
  components: {
    dPeriod,
  },
  data() {
    return {
      selectedTags: [],
    };
  },
  created() {
    this.$store.subscribeAction((action) => {
      if (action.type === "streaks/add") {
        [];
      }
    });
  },
  props: {
    tags: {
      type: Array,
      required: true,
    },
    active: {
      type: Boolean,
      default: true,
    },
    multi: {
      type: Boolean,
      default: false,
    },
    needsAnS: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    selectTag(tag) {
      if (this.multi) {
        if (this.selectedTags.includes(tag)) {
          this.selectedTags = this.selectedTags.filter((item) => item !== tag);
        } else {
          this.selectedTags.push(tag);
        }
      } else {
        this.selectedTags = tag;
      }
      this.$emit("input", this.selectedTags);
    },
  },
  watch: {
    tags() {
      this.selectedTags = [];
      this.$emit("input", this.selectedTags);
    },
  },
};
</script>

<style lang="scss" scoped>
.tag-list {
  gap: 0.5rem;
  transition: opacity 200ms linear;

  &:not(.active) {
    user-select: none;
    opacity: 0.1;
    pointer-events: none;
  }

  &.multi {
    border-radius: 15px;

    display: flex;
    flex-wrap: wrap;

    & > * {
      width: 2rem;
      font-size: 0.8rem;
    }
  }
}
</style>
