<template>
  <div class="header">
    <dc-chart-base
      :title="$moment(date).format('[WK] w')"
      :subTitle="`${$moment(date).startOf('week').format('DD.MM.YY')} - ${$moment(date).endOf('week').format('DD.MM.YY')}`"
      :progress="progress(date)"
      :graphData="week(date)"
      icon="calendar-week"
    />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import dcChartBase from "@/components/charts/dcChartBase.vue";

export default {
  name: "history-period-header",
  components: {
    dcChartBase,
  },
  computed: {
    ...mapGetters("stats/graphData", ["week"]),
    ...mapGetters("stats/progress", { progress: "week" }),
  },
  props: {
    date: {
      type: Object,
      required: true,
    },
  },
};
</script>

