import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faChevronLeft,
  faPlus,
  faEdit,
  faTrash,
  faCalendarWeek,
  faCalendar,
  faCalendarDay,
  faCalendarAlt,
  faChevronRight,
  faPalette,
  faTimes,
  faCircleNotch,
  faSpinner,
  faBars,
  faHome,
  faInfo,
  faCog,
  faHistory,
  faChartArea,
  faCalendarCheck,
  faMeteor,
  faBrush,
  faSun,
  faMoon,
  faCheck,
  faTrademark,
  faCalendarTimes,
  faPlusCircle,
  faCheckCircle,
  faTh,
  faThList,
  faList,
  faThLarge,
  faInfinity,
  faTrophy,
  faStopwatch,
  faAngleDown,
  faAngleUp,
  faWindowMinimize,
} from "@fortawesome/free-solid-svg-icons";
import {
  faCheckSquare,
  faSquare,
  faListAlt,
} from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

const FontAwesome = {
  install(Vue) {
    library.add(
      faChevronLeft,
      faChevronRight,
      faPlus,
      faEdit,
      faTrash,
      faCheckSquare,
      faSquare,
      faCalendar,
      faCalendarAlt,
      faCalendarDay,
      faCalendarWeek,
      faPalette,
      faTimes,
      faCircleNotch,
      faSpinner,
      faBars,
      faHome,
      faInfo,
      faCog,
      faHistory,
      faChartArea,
      faCalendarCheck,
      faCalendarTimes,
      faMeteor,
      faCalendarAlt,
      faBrush,
      faSun,
      faMoon,
      faCheck,
      faTrademark,
      faPlusCircle,
      faCheckCircle,
      faTh,
      faList,
      faThLarge,
      faThList,
      faSquare,
      faListAlt,
      faInfinity,
      faTrophy,
      faStopwatch,
      faAngleDown,
      faAngleUp,
      faWindowMinimize
    );

    Vue.component("font-awesome-icon", FontAwesomeIcon);
  },
};

export default FontAwesome;
