<template>
  <div v-if="icon">
    <p :class="icon" v-if="icon === 'same'">-</p>
    <d-icon :class="icon" v-else small :icon="icon" />
  </div>
</template>

<script>
export default {
  name: "dc-up-down",
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      icon: null,
    };
  },
  created() {
    const result = this.$store.getters["stats/item/upDown"](this.id);
    this.icon = result === 0 ? "same" : result > 0 ? "angle-up" : "angle-down";
  },
};
</script>

<style lang="scss" scoped>
.angle-up {
  color: var(--color-prime);
}
.angle-down {
  color: var(--color-danger);
}
.same {
  font-size: 1rem;
  color: var(--color-text);
}
</style>