<template>
  <div :class="['menu-item', { alt, selected: isSelected }]" @click="routeTo">
    <div v-if="!alt" :class="['arr', { selected: isSelected }]"></div>
    <d-icon
      xLarge
      id="icon"
      :class="['menu-item__icon', { alt, selected: isSelected, prime }]"
      :icon="item.icon"
    />
    <p class="menu-item__title" v-if="!alt">{{ item.name }}</p>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "d-menu-item",
  props: {
    item: {
      type: Object,
      required: true,
    },
    alt: {
      type: Boolean,
      required: false,
    },
    prime: {
      type: Boolean,
      required: false,
    },
    selected: {
      type: Boolean,
      required: false,
    },
  },
  computed: {
    currentPath() {
      return this.$route.path;
    },
    isSelected() {
      return this.selected
        ? this.selected
        : this.currentPath === this.item.path;
    },
  },
  methods: {
    ...mapActions("menu", {
      closeMenu: "close",
    }),
    routeTo() {
      if (this.item.link)
        this.$store.dispatch(this.item.link, this.item.payload);
      else if (this.currentPath !== this.item.path)
        this.$router.push(this.item.path);
      else this.closeMenu();
    },
  },
};
</script>

<style lang="scss" scoped>
.menu-item {
  opacity: 0.9;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--color-text-btn);
  width: 5.7rem;
  transition: all 500ms ease-in-out;
  padding: 0 1rem;
  padding-left: 1.25rem;
  position: relative;
  &.selected {
    & > * {
      color: var(--color-bg);
    }
  }
  &:active &__icon:not(.alt) {
    transition: all 50ms ease-in-out;
    transform: scale(0.85);
  }

  &.alt {
    width: 2rem;
  }

  & .arr {
    position: absolute;
    top: calc(50% - 1.34rem);
    left: 0;
    height: 1.1rem;
    width: 1.07rem;
    clip-path: polygon(0 0, 0% 100%, 100% 50%);
    transform: translateX(-100%);
    transition: transform 650ms ease;
    background-color: var(--color-bg);
    &.selected {
      transform: translateX(0%) rotate(-2deg);
    }
  }

  &__icon.alt {
    position: relative;
    padding-bottom: 0.4rem;
    margin-bottom: -0.44rem;
    color: var(--color-text);
    font-size: 1.5rem;
    transition: all 300ms ease-in-out;

    &.selected {
      color: var(--color-prime);
      // border-bottom: 2px solid var(--color-prime);
    }
  }

  &__icon.prime {
    color: var(--color-prime);
  }

  &__title {
    opacity: 0.9;
    margin-top: 0.6rem;
    font-weight: 500;
    position: relative;
    color: var(--color-text-btn);
    font-size: 0.95rem;
    text-transform: capitalize;

    & span {
      position: absolute;
      right: 110%;
      bottom: 0;
    }
  }
}
</style>
