<template>
  <div
    :class="['card', { done: isConfirmed(streak), list }]"
    @click="openStreak"
    @mousedown="press"
    @mouseleave="release"
    @mouseup="release"
    @touchstart="press"
    @touchend="release"
    @touchcancel="release"
  >
    <div class="hold-progress"></div>
    <div class="card__header">
      <h3 class="title">{{ streak.title }}</h3>
      <d-tag :item="streak" :tag="streak.period[0]" />
    </div>
    <div class="card__footer">
      <dc-progress-circle small class="p-circle" :progress="this.streak.progress" />
    </div>
  </div>
</template>

<script>
import dcProgressCircle from "@/components/charts/dcProgressCircle.vue";
// import { getRndNumber } from "@/helper/helper.js";
import { mapGetters } from "vuex";

export default {
  name: "d-card",
  created() {
    // const rnd = getRndNumber(100);
    // this.progress = rnd < 20 ? 100 : this.tempProgress(this.streak.id);
  },
  components: {
    dcProgressCircle,
  },
  computed: {
    ...mapGetters({
      isConfirmed: "streaks/isItemConfirmed",
      // tempProgress: "stats/item/progress",
    }),
  },
  props: {
    streak: {
      type: Object,
      required: true,
    },
    list: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      progress: 0,
      timeout: false,
      prevScrollpos: 0,
    };
  },
  methods: {
    openStreak() {
      this.$router.push(`/streak/${this.streak.id}`);
    },

    comfirmStreak() {
      const currentScrollPos = window.pageYOffset;
      if (Math.abs(this.prevScrollpos - currentScrollPos) < 3) {
        this.$store.dispatch("streaks/confirmCurrent", this.streak.id);
      }
    },
    press() {
      if (!this.timeout) {
        this.prevScrollpos = window.pageYOffset;
        this.timeout = setTimeout(() => this.comfirmStreak(), 1000);
      }
    },
    release() {
      clearTimeout(this.timeout);
      this.timeout = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.hold-progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%);
  transition: transform 800ms linear;
  z-index: -1;
}

.card {
  overflow: hidden;
  position: relative;
  @include card;
  margin-left: 0.5rem;
  margin-bottom: 0.5rem;
  height: 4.7rem;

  &:active .hold-progress {
    background-color: var(--color-prime-light);
    transform: translateX(0%);
  }

  & .title {
    font-size: 0.85rem;
    margin-bottom: 2px;
  }

  &__footer {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: flex-end;

    .p-circle {
      justify-content: space-between;
      flex-grow: 1;
    }
  }

  &.list {
    height: 100%;
    flex-direction: row;
    align-items: center;
    padding-top: 0.15rem;
    padding-bottom: 0.2rem;
  }

  &.list &__header {
    flex-grow: 2;
    width: 100%;
    display: flex;
    align-items: center;

    & .title {
      margin-bottom: 0px;
      margin-right: 0.4rem;
    }
  }

  &.list &__footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .p-circle {
      flex-grow: 0;
    }
  }
}
</style>
