import { setLocal, getLocal } from "@/plugins/SaveLocal.js";

export default {
  namespaced: true,
  state: {
    open: false,
    menu: [
      { name: "light", icon: "sun", link: "themes/setMode", payload: "light" },
      { name: "dark", icon: "moon", link: "themes/setMode", payload: "dark" },
      {
        name: "color",
        icon: "brush",
        link: "themes/setMode",
        payload: "color",
      },
    ],
    colors: {
      light: ["blue", "purple", "green", "red"],
      dark: ["blue", "purple", "green", "lime"],
      color: ["purple", "bubblegum", "sky", "green"],
    },
    selectedTheme: "blue",
    selectedMode: "light",
    usedMode: "light",
    localPath: "themes",
  },
  mutations: {
    CLOSE(state) {
      state.open = false;
    },
    OPEN(state) {
      state.selectedMode = state.usedMode;
      state.open = true;
    },
    SET(state, theme) {
      const themeStr = `${state.selectedMode} ${theme}`;
      window.document.documentElement.className = themeStr;
      state.usedMode = state.selectedMode;
      state.selectedTheme = theme;
    },
    SET_MODE(state, payload) {
      state.selectedMode = payload;
    },
    SET_LOCAL(state) {
      setLocal(
        { mode: state.selectedMode, theme: state.selectedTheme },
        state.localPath
      );
    },
  },
  actions: {
    close({ commit }) {
      commit("CLOSE");
    },
    open({ commit }) {
      commit("OPEN");
    },
    set({ commit }, theme) {
      commit("SET", theme);
      commit("SET_LOCAL");
    },
    setMode({ commit }, mode) {
      commit("SET_MODE", mode);
    },
    init({ commit, state }) {
      let data = getLocal(state.localPath);
      if (!data) {
        data = { mode: state.selectedMode, theme: state.selectedTheme };
      }
      commit("SET_MODE", data.mode);
      commit("SET", data.theme);
      commit("SET_LOCAL");
    },
  },
};
