export default {
  namespaced: true,
  state: {
    open: false,
    items: [
      // { name: "Home", icon: "home", path: "/" },
      { name: "Themes", icon: "palette", link: "themes/open" },
      { name: "About", icon: "info", path: "/about" },
      { name: "Settings", icon: "cog", path: "/settings" },
    ],
  },
  mutations: {
    TOGGLE(state) {
      state.open = !state.open;
    },
    CLOSE(state) {
      state.open = false;
    },
  },
  actions: {
    toggle({ commit, dispatch, state }) {
      commit("TOGGLE");
      if (!state.open) {
        setTimeout(() => {
          dispatch("themes/close", null, { root: true });
        }, 500);
      }
    },
    close({ commit, dispatch }) {
      commit("CLOSE");
      setTimeout(() => {
        dispatch("themes/close", null, { root: true });
      }, 500);
    },
  },
};
