<template>
  <div :class="['progress-circle', {large, small}]">
    <dc-progress class="title" :small="small" perc :progress="progress" />
    <dc-circle
      v-if="progress < 100"
      :small="small"
      :large="large"
      class="circle"
      :progress="progress"
    />

    <d-icon v-else icon="meteor" class="fa-flip-vertical fa-flip-horizontal" id="icon" xLarge />
  </div>
</template>

<script>
import dcCircle from "@/components/charts/dcCircle.vue";
import dcProgress from "@/components/charts/dcProgress.vue";

export default {
  name: "dc-progress-circle",
  components: {
    dcCircle,
    dcProgress,
  },
  props: {
    progress: {
      type: Number,
      default: 0,
    },
    small: {
      type: Boolean,
      default: false,
    },
    large: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style lang="scss" scoped>
.progress-circle {
  display: flex;
  align-items: flex-end;

  .title {
    margin-right: 0.6rem;
  }

  &.large {
    align-items: flex-end;
    justify-content: flex-end;
    flex-direction: row-reverse;
    width: 100%;

    .title {
      margin-left: 0.4rem;
      margin-right: 0;
    }
  }

  #icon {
    color: var(--color-prime);
    // margin-bottom: -2px;
    font-size: 0.99rem;
  }
}
</style>