<template>
  <d-icon
    id="backIcon"
    large
    @click.native="goBack"
    size="xs"
    icon="chevron-left"
  />
</template>

<script>
  export default {
    name: "arrow-back",
    methods: {
      goBack() {
        this.$router.go(-1);
      },
    },
  };
</script>

<style scoped>
  #backIcon {
    font-size: 1rem;
  }
</style>
