<template>
  <div class="container">
    <d-label class="list-label" content="History" />
    <d-list v-if="history">
      <d-confirm
        v-for="date in history"
        :key="date.id"
        :confirm="date"
        :streakId="streakId"
        :mode="streak.period[0].mode"
        history
      ></d-confirm>
    </d-list>
  </div>
</template>

<script>
import dConfirm from "@/components/dConfirm.vue";
import { mapGetters } from "vuex";

export default {
  name: "StreakHistory",
  components: {
    dConfirm,
  },
  computed: {
    ...mapGetters("streaks", ["getStreakById"]),
  },
  created() {
    this.streakId = this.$route.params.id;
    this.streak = this.getStreakById(this.streakId);
    this.title = this.streak.title;
    this.history = this.$store.state.history.list[this.streakId];
  },
  data() {
    return {
      streakId: null,
      streak: null,
      history: null,
      title: "",
    };
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
.history-list {
  gap: 5px;
  margin: 5px 0.8rem;

  grid-template-columns: repeat(auto-fit, minmax(2rem, 1fr));
  display: grid;
}
</style>
