<template>
  <div class="menu-toggle-container" @click="toggleMenu">
    <div :class="['line', { isMenuOpen }]"></div>
    <div :class="['line-short', { isMenuOpen }]"></div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  name: "d-menu-toggle",
  methods: {
    ...mapActions("menu", {
      toggleMenu: "toggle",
    }),
  },
  computed: {
    ...mapState({
      isMenuOpen: (state) => state.menu.open,
    }),
  },
};
</script>

<style lang="scss" scoped>
.menu-toggle-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  z-index: 9999999;
}

.line,
.line-short {
  border-radius: 0px;
  background-color: var(--color-text);
  height: 3px;
  transition: transform 500ms cubic-bezier(0.55, 0, 0.1, 1);

  &.isMenuOpen {
    transition: transform 500ms cubic-bezier(0.29, 1.45, 0.55, 0.99);
    width: 16px;
  }
}

.line {
  width: 1.2rem;
  margin-bottom: 5px;

  &.isMenuOpen {
    transform: rotate(45deg) translate(1px, 0px);
  }
}
.line-short {
  width: 0.8rem;

  &.isMenuOpen {
    transform: rotate(-45deg) translate(5.5px, -4.7px);
  }
}
</style>
