<template>
  <div class="streaks">
    <d-streak-list icon="calendar-alt" :content="headline" :list="list" />
  </div>
</template>

<script>
import dStreakList from "@/components/dStreakList.vue";

import { mapGetters } from "vuex";

export default {
  name: "Streaks",
  components: {
    dStreakList,
  },
  mounted() {
    const mode = this.$route.params.id;
    const sortByDone = (a, b) =>
      this.isItemConfirmed(a) - this.isItemConfirmed(b);

    this.headline = mode === "day" ? "today" : mode;

    if (mode === "all") {
      this.list = this.$store.state.streaks.list;
    } else {
      this.list = this.getByMode(mode);
    }

    this.list.sort(sortByDone);
  },
  computed: {
    ...mapGetters("streaks", ["getByMode", "isItemConfirmed"]),
  },
  data() {
    return {
      list: [],
      headline: "",
    };
  },
};
</script>

<style lang="scss" scoped>
.streaks {
  margin-top: 4rem;
  margin-bottom: 2rem;
}
</style>

