<template>
  <div ref="header" class="header container-s">
    <arrow-back :class="{ hidden: !back }" />
    <transition name="slide-menu" mode="out-in">
      <h3 @click="back ? $router.go(-1) : null" :class="['title', { back }]">{{ title }}</h3>
    </transition>
    <d-menu-toggle :class="['menu-btn  container-s', { hidden: !menu }]" />
  </div>
</template>

<script>
import dMenuToggle from "@/components/menu/dMenuToggle.vue";

export default {
  name: "d-header",
  components: {
    dMenuToggle,
  },
  props: {
    title: {
      type: String,
      default: "",
    },
    menu: {
      type: Boolean,
      default: false,
    },
    back: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      prevScrollpos: 0,
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
    this.prevScrollpos = window.pageYOffset;
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    /* eslint-disable */
    handleScroll(event) {
      if (!this.$store.state.menu.open) {
        const header = this.$refs.header;
        const currentScrollPos = window.pageYOffset;
        if (this.prevScrollpos > currentScrollPos) {
          header.style.top = "0";
        } else {
          if (window.pageYOffset > 50) {
            header.style.top = "-50px";
          }
        }
        this.prevScrollpos = currentScrollPos;
      }
    },
    /* eslint-enable */
  },
};
</script>

<style lang="scss" scoped>
.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  background-color: var(--color-bg);
  z-index: 9999999;
  padding-left: 0.9rem;
  padding-top: 1.5rem;
  background: linear-gradient(
    var(--color-bg) 10%,
    rgba($color: #000000, $alpha: 0) 80%
  );
  transition: top 350ms cubic-bezier(0.55, 0, 0.1, 1);

  & .title {
    margin-top: -1px;
    text-transform: uppercase;
    font-size: 1.21rem;
    line-height: 1;

    &.back {
      border-left: unset;
      padding: unset;
      margin-left: 0.7rem;
    }
  }
}

.menu-btn {
  position: absolute;
  right: 0;
  top: 1px;
  padding-top: 1.5rem;

  min-width: 1.1rem;
}

.hidden {
  display: none;
}
</style>
