<template>
  <div class="menu-overlay" @click.self="closeMenu">
    <div class="menu-container">
      <div class="arrow"></div>

      <div class="menu-sub-container">
        <div :class="['menu', { themesOpen }]">
          <div class="menu__items">
            <div :class="['menu__list', { themesOpen }]">
              <d-menu-item
                class="menu__item"
                :item="item"
                v-for="item in menuItems"
                :key="item.name"
              />
            </div>
            <div :class="['menu__list', 'menu__list--theme', { themesOpen }]">
              <d-menu-item
                class="menu__item"
                :item="item"
                v-for="item in themesItems"
                :selected="item.name === selectedMode"
                :key="item.name"
              />
            </div>
          </div>
        </div>
      </div>

      <div class="themeMenu-container">
        <transition name="slide-menu">
          <d-theme-menu v-if="themesOpen" />
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import dMenuItem from "@/components/menu/dMenuItem.vue";
import dThemeMenu from "@/components/menu/dThemeMenu.vue";
import { mapActions, mapState } from "vuex";

export default {
  name: "d-menu",
  computed: {
    ...mapState({
      menuItems: (state) => state.menu.items,
      themesItems: (state) => state.themes.menu,
      selectedMode: (state) => state.themes.selectedMode,
      themesOpen: (state) => state.themes.open,
    }),
  },

  components: {
    dMenuItem,
    dThemeMenu,
  },
  methods: {
    ...mapActions("menu", {
      closeMenu: "close",
    }),
    ...mapActions("themeMenu", {
      openThemeMenu: "open",
      closeThemeMenu: "close",
    }),
  },
  watch: {
    $route() {
      this.closeMenu();
    },
  },
};
</script>

<style lang="scss" scoped>
.menu-overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;

  top: 0;
  left: 0;
}

.menu-container {
  position: absolute;
  top: 3.5rem;
  right: 2.4rem;
  z-index: 999999;
  transform: rotate(1deg);
}

.menu-sub-container {
  transform-origin: top right;
  transform: rotate(-3deg);
  overflow: hidden;
  border-radius: 20px;
  border-bottom-left-radius: 22px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  // background-color: yellow;
}
.themeMenu-container {
  transform-origin: top right;
  transform: rotate(-3deg);
  border-radius: 20px;

  position: absolute;
  top: 0.35rem;
  right: 100%;
  height: calc(100% - 0.28rem);
  margin-right: 6px;
}

.theme-menu {
  border-radius: 23px;
  height: 100%;
}

.menu {
  transform-origin: top right;
  transform: rotate(3deg);
  border-radius: 20px;
  border-bottom-left-radius: 34px;

  // clip-path: polygon(65% -55rem, 100% 0%, 100% 100%, 0 100%);
  // margin-bottom: -1rem;
  color: var(--color-text-btn);
  background-color: var(--color-prime);
  padding-top: 0rem;
  padding-bottom: 0.1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__items {
    overflow: hidden;
    position: relative;
  }

  &__item:not(:last-of-type) {
    margin-bottom: 2.5rem;
  }
}

.arrow {
  position: absolute;
  bottom: 94%;
  right: -1px;
  width: 0;
  height: 0;
  border-style: solid;
  // border-radius: 2px;
  border-width: 0 0 2.2rem 2.2rem;
  transform: rotate(5deg);
  border-color: transparent transparent var(--color-prime-light) transparent;
}

.menu__list {
  transform-origin: top right;
  transform: rotate(-1deg);
  padding: 2.2rem 0 2.5rem 0;
  transition: transform 500ms ease;

  &.themesOpen {
    transform: translateY(100%) rotate(-1deg);
  }

  &--theme {
    position: absolute;
    z-index: 9999999;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;

    transform-origin: top right;
    transform: translateY(-100%) rotate(-1deg);
    &.themesOpen {
      transform: translateY(0) rotate(-1deg);
    }
  }
}
</style>
