import Vue from "vue";
import Vuex from "vuex";

import notifier from "./plugins/notifier";

import streaks from "./streaks";
import snackbar from "./snackbar";
import repeats from "./repeats";
import themes from "./themes";
import menu from "./menu";
import bottomMenu from "./bottomMenu";
import sidebar from "./sidebar";
import history from "./history";
import stats from "./stats";

Vue.use(Vuex);

export default new Vuex.Store({
  namespaced: true,
  state: {
    transitionName: "fade",
    headerTitle: "",
    headerMenu: true,
    headerBack: false,
  },
  mutations: {
    SET_TRANSITION(state, transitionName) {
      state.transitionName = transitionName;
    },
    SET_HEADER(state, header) {
      state.headerTitle = header.title;
      state.headerBack = header.back;
      state.headerMenu = header.menu;
    },
  },
  actions: {
    setTransition({ commit }, transitionName) {
      commit("SET_TRANSITION", transitionName);
    },
    setHeader({ commit }, header) {
      commit("SET_HEADER", header);
    },
  },
  getters: {},
  modules: {
    streaks,
    snackbar,
    repeats,
    themes,
    menu,
    bottomMenu,
    sidebar,
    history,
    stats,
  },
  plugins: [notifier],
});
