<template>
  <div class="period-container">
    <slot></slot>
    <div class="period">
      <div class="period__date">
        <d-label small icon="calendar-day" :content="date.format('ddd')" />
        <d-label sub :content="date.format('DD.MM.GG')" />
      </div>

      <div class="period__items">
        <div class="items__confiremd">
          <d-history-period-list :items="done" confirmed />
        </div>
        <div class="items__missed">
          <d-history-period-list :items="missed" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dHistoryPeriodList from "./dHistoryPeriodList.vue";

export default {
  name: "d-history-period",
  components: {
    dHistoryPeriodList,
  },
  props: {
    date: {
      type: Object,
      required: true,
    },
    items: {
      type: Array,
      required: true,
    },
  },
  computed: {
    done() {
      return this.items.filter((item) => item.isConfirmed);
    },
    missed() {
      return this.items.filter((item) => !item.isConfirmed);
    },
  },
};
</script>

<style lang="scss" scoped>
.period {
  display: flex;
  border-top: 1px dashed var(--color-border-light);

  &__date {
    padding: 0.9rem 0.9rem 0.7rem 0.9rem;

    background-color: var(--color-bg-lighten);
    min-width: 5.3rem;
    padding-right: 0.5rem;
  }
  &__items {
    padding: 0.7rem 0.8rem 0.8rem 0.6rem;

    flex-grow: 2;

    display: flex;

    & > * {
      width: 50%;
    }
  }
}
</style>