import { timetravel } from "@/helper/timetravel.js";
import moment from "moment";

export default {
  namespaced: true,
  state: {
    days: [0, 1, 2, 3, 4, 5, 6],
    option: ["every", "even", "odd", "some"],
    mode: ["day", "week", "month"],
    dayShort: ["sun", "mon", "tus", "wed", "thu", "fri", "sat"],
    monthShort: [
      "jan",
      "feb",
      "mar",
      "apr",
      "may",
      "jun",
      "jul",
      "aug",
      "sep",
      "oct",
      "nov",
      "dec",
    ],
    weeks: Array.from([...Array(52).keys()], (x) => `${x + 1}`),
  },
  getters: {
    someToInt: (state) => (some, mode) => {
      const list =
        mode === "day"
          ? state.dayShort
          : mode === "month"
          ? state.monthShort
          : state.week;
      return some.map((item) => list.indexOf(item));
    },
  },

  mutations: {},
  actions: {},
  modules: {},
};
