<template>
  <font-awesome-icon
    :class="['icon', { small, large, xLarge }]"
    v-if="icon"
    :icon="[prefix, icon]"
    :size="size"
  />
</template>

<script>
import { size } from "@/components/mixins/main.js";

export default {
  name: "d-icon",
  mixins: [size],
  props: {
    icon: {
      type: String,
      default: "",
    },
    prefix: {
      type: String,
      default: "fas",
    },
    size: {
      type: String,
      default: "xs",
    },
    xLarge: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style lang="scss" scoped>
.icon {
  font-size: var(--icon-size);
  fill: var(--color-text);
  stroke: var(--color-text);
}

.small {
  font-size: var(--icon-size-s);
}

.large {
  font-size: var(--icon-size-l);
}
.xLarge {
  font-size: var(--icon-size-xl);
}
</style>
