<template>
  <div
    v-if="confirm && streakId && mode"
    :class="['confirm', { confirmed: confirm.isConfirmed || confirmOverride }]"
    @mousedown="press"
    @mouseleave="release"
    @mouseup="release"
    @touchstart="press"
    @touchend="release"
    @touchcancel="release"
  >
    <div :class="['icon-container', { history }]">
      <d-icon small v-if="confirm.isConfirmed || confirmOverride" icon="calendar-check" class="ok" />
      <d-icon small class="danger" v-else-if="history" icon="calendar-times" />
      <d-icon small v-else icon="calendar" />
    </div>

    <div class="date">
      <p>{{ toHumanDate }}</p>
    </div>
    <div class="shortDate">
      <span class="shortDate--light">{{ toHumanDateShort.split(" ")[0] }}</span>
      <span class="shortDate--prime">{{ toHumanDateShort.split(" ")[1] }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "d-confirm",
  props: {
    confirm: {
      type: Object,
      required: true,
    },
    streakId: {
      type: String,
      required: true,
    },
    mode: {
      type: String,
      required: true,
    },
    history: {
      type: Boolean,
      default: false,
    },
    current: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      timeout: false,
      confirmOverride: false,
    };
  },
  computed: {
    toHumanDate() {
      if (this.mode === "day") {
        const isSameDay = this.$timetravel.isSameDay(this.confirm.date);
        const isNextDay = this.$timetravel.isNextDay(this.confirm.date);
        const isYesterday = this.$timetravel.isYesterday(this.confirm.date);
        return isSameDay
          ? "Today"
          : isYesterday
          ? "Yesterday"
          : isNextDay
          ? "Tomorrow"
          : this.confirm.date.format("dddd");
      } else if (this.mode === "week") {
        const isSameWeek = this.$timetravel.isSameWeek(this.confirm.date);
        const isLastWeek = this.$timetravel.isLastWeek(this.confirm.date);
        const isNextWeek = this.$timetravel.isNextWeek(this.confirm.date);
        return isSameWeek
          ? "This Week"
          : isLastWeek
          ? "Last Week"
          : isNextWeek
          ? "Next Week"
          : this.confirm.date.format("wo [Week]");
      } else if (this.mode === "month") {
        const isSameMonth = this.$timetravel.isSameMonth(this.confirm.date);
        const isLastMonth = this.$timetravel.isLastMonth(this.confirm.date);
        const isNextMonth = this.$timetravel.isNextMonth(this.confirm.date);
        return isSameMonth
          ? "This Month"
          : isLastMonth
          ? "Last Month"
          : isNextMonth
          ? "Next Month"
          : this.confirm.date.format("MMMM");
      }
      return "";
    },
    toHumanDateShort() {
      if (this.mode === "day") {
        return this.confirm.date.format("DD MMM");
      } else if (this.mode === "week") {
        return this.confirm.date.format(". MMMM");
      } else if (this.mode === "month") {
        return this.confirm.date.format(". MMMM");
      }
      return "";
    },
  },
  methods: {
    comfirmStreak() {
      if (this.history) {
        this.$store.dispatch("history/confirm", {
          streakId: this.streakId,
          item: this.confirm,
        });
        this.confirmOverride = true;
      } else if (this.current) {
        this.$emit("confirmCurrent");
      }
    },
    press() {
      if (!this.timeout) {
        this.timeout = setTimeout(
          () => (!this.confirm.isConfirmed ? this.comfirmStreak() : null),
          200
        );
      }
    },
    release() {
      clearTimeout(this.timeout);
      this.timeout = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.confirm {
  @include box;
  display: flex;
  font-size: 0.8rem;
  font-weight: lighter;
  color: var(--color-prime);
  font-weight: 600;
  padding: 4px 0 0 4px;

  height: 90px;
  overflow: hidden;
  position: relative;
}

.date {
  margin-left: 0.3rem;
  // margin-top: -1px;
}

.icon-container {
  font-size: 0.7rem;
  .ok {
    color: var(--color-ok);
  }
  .danger {
    color: var(--color-danger);
  }
}

.shortDate {
  opacity: 0.2;
  position: absolute;
  bottom: -0.6rem;
  font-style: italic;
  letter-spacing: -2px;
  right: 0.1rem;
  font-size: 2.5rem;
  color: var(--color-prime);
  font-weight: 700;
  // width: 100%;

  &--light {
    z-index: 2;
    position: relative;
    letter-spacing: -6px;
  }

  &--prime {
    color: var(--color-muted);
    margin-left: 0.3rem;
    letter-spacing: -3px;
    z-index: -1;
  }
}
</style>
