const notifier = (store) => {
  store.subscribeAction({
    after: (action) => {
      let content;
      const color = "ok";

      switch (action.type) {
        case "streaks/init":
          content = "Data loaded";
          break;
        case "streaks/add":
          content = "Streak added";
          break;
        case "streaks/edit":
          content = "Streak edited";
          break;
        case "streaks/delete":
          content = "Streak deleted";
          break;
        case "streaks/confirm":
          content = "God Job !!";
          break;

        default:
          break;
      }
      if (content) {
        // store.commit("snackbar/showMessage", { content, color });
      }
    },
    error: (action, state, error) => {
      let content = "Something went wrong";
      const color = "danger";

      store.commit("snackbar/showMessage", { content, color });
      console.log(action);
      console.log(`error action ${action.type}`);
      console.error(error);
    },
  });
};

export default notifier;
