export const route = {
  props: {
    route: {
      type: String,
      default: "",
    },
  },
  methods: {
    routeTo() {
      this.$router.push(`/${this.route}`);
    },
  },
};

export const size = {
  props: {
    xSmall: {
      type: Boolean,
      default: false,
    },
    small: {
      type: Boolean,
      default: false,
    },
    large: {
      type: Boolean,
      default: false,
    },
  },
};

export default { route, size };
