<template>
  <div class="home">
    <transition name="fade-slow">
      <div v-if="isFetchingStreaks" key="loading" class="loading-container">
        <d-loading />
      </div>

      <div v-else key="lists">
        <dc-chart class="chart" />

        <div class="list-container">
          <d-streak-list
            route="streaks/day"
            icon="calendar-day"
            class="steak-list"
            content="today"
            :list="filterDone(today)"
          />
          <d-streak-list
            route="streaks/week"
            icon="calendar-week"
            class="steak-list"
            content="week"
            :list="filterDone(week)"
          />
          <d-streak-list
            route="streaks/month"
            icon="calendar-alt"
            class="steak-list"
            content="month"
            :list="filterDone(month)"
          />
        </div>
        <div v-if="!isFetchingStreaks" class="all-link">
          <d-link route="streaks/all" icon="calendar" class="allLink" />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import dStreakList from "@/components/dStreakList.vue";
import dcChart from "@/components/charts/dcChart.vue";
import { mapGetters, mapActions, mapState } from "vuex";

export default {
  name: "Home",
  components: {
    dStreakList,
    dcChart,
  },
  computed: {
    ...mapGetters("streaks", ["today", "week", "month", "isItemConfirmed"]),
    ...mapState({
      isFetchingStreaks: (state) => state.streaks.fetching,
    }),
  },
  methods: {
    filterDone(list) {
      return list.filter((item) => !this.isItemConfirmed(item));
    },
  },
};
</script>

