<template>
  <div>
    <!-- <div v-if="open" class="sidebar-container" @click.self="close"></div> -->
    <div :class="['sidebar', { open }]" @click.self="!open ? openSidebar : null">
      <span @click="toggle" class="toggle">
        <d-icon large :class="['plus']" icon="plus" />
        <p>ADD</p>
      </span>

      <d-add :class="['content', { open }]" />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "d-sidebar",

  computed: {
    ...mapState({
      open: (state) => state.sidebar.open,
    }),
  },
  mounted() {
    this.$el.classList.add("closed");
  },
  methods: {
    ...mapActions("sidebar", {
      close: "close",
      toggle: "toggle",
      openSidebar: "open",
    }),
  },
};
</script>

<style lang="scss" scoped>
.sidebar-container {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 99999997;

  top: 0;
  left: 0;
}
.content {
  position: relative;
  height: 100%;
  opacity: 0;
  // transition: opacity 200ms 600ms ease-in-out;
  transition: opacity 200ms 700ms ease-in-out;

  flex-grow: 2;
  z-index: 99999999;

  &.open {
    transition: opacity 200ms 700ms ease-in-out;
    transition-delay: 700ms;
    opacity: 1;
  }
}
.sidebar {
  position: fixed;
  bottom: 0;
  right: 0;
  background-color: var(--color-bg-alpha);
  border-top-left-radius: 25px;
  z-index: 99999998;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  transition: transform 650ms cubic-bezier(0.165, 0.84, 0.44, 1),
    backdrop-filter 0ms 0ms;

  transform: translate(calc(calc(100vw - 3.9rem)), calc(calc(100vh - 4rem)));
  border-top: 2px solid transparent;

  &.open {
    transform: translate(0, 0);
    border-top: 2px solid var(--color-prime);
    backdrop-filter: blur(1px);
    transition: transform 650ms cubic-bezier(0.165, 0.84, 0.44, 1),
      backdrop-filter 5ms 580ms linear, border 150ms 550ms ease-in-out;
    border-radius: 0;

    & .toggle {
      transform: translate(-0.3rem, 0.3rem);
    }

    & .plus {
      transform: rotate(-45deg);
    }
    & .toggle p {
      opacity: 1;
    }
  }

  & .toggle {
    display: inline-flex;
    align-items: center;
    padding-bottom: 1rem;

    & p {
      transition: opacity 250ms 70ms ease;
      margin-bottom: -1.2rem;
      opacity: 0;
    }

    & .plus {
      transition: transform 1000ms ease;
      color: var(--color-prime);
      margin: 1.2rem 0.7rem 0rem 1.4rem;
    }
  }
}
</style>
