<template>
  <div :class="['progress', {small}]">
    <div v-if="check" class="progress__item">
      <p class="progress-text">{{ progress }}</p>
      <d-icon class="icon__pos" xLarge icon="check" />
    </div>
    <div v-if="times" class="progress__item">
      <p class="progress-text">{{ progress }}</p>
      <d-icon class="icon__neg" xLarge icon="times" />
    </div>
    <div v-if="perc" class="progress__item__perc">
      <p class="progress-text">{{ progress }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "dc-progress",
  props: {
    progress: {
      type: Number,
      default: 0,
    },
    check: {
      type: Boolean,
      default: false,
    },
    perc: {
      type: Boolean,
      default: false,
    },
    times: {
      type: Boolean,
      default: false,
    },
    small: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style lang="scss" scoped>
.progress {
  &__item {
    display: flex;
    align-items: flex-end;

    .icon {
      margin-left: 0.3rem;

      &__pos {
        font-size: 0.9rem;
        margin-bottom: 1px;
        color: var(--color-prime);
        margin-right: -4px;
      }
      &__neg {
        font-size: 1.05rem;

        color: var(--color-danger);
      }
    }
    &__perc {
      p::after {
        content: "%";
        color: var(--color-prime-light);
        font-size: 1rem;
        margin-left: 1px;
      }
    }
  }
}
.progress.small {
  .progress-text {
    font-size: 0.95rem;
  }

  .icon {
    margin-left: 4px;
    &__pos {
      font-size: 0.7rem;
    }
    &__neg {
      font-size: 0.8rem;
    }
  }
  & .progress__item__perc p::after {
    content: "%";
    font-size: 0.75rem;
  }
}
</style>