<template>
  <div :class="['color-label']" @click="changeTheme(color)" v-if="selectedMode && selectedTheme">
    <div
      :class="[
        selectedMode,
        color,
        'color-label__color',
        { active },
      ]"
    ></div>
    <div :class="['color-label__title', { active }]">{{ color }}</div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  name: "d-color-label",
  props: {
    color: {
      type: String,
      default: "",
    },
  },
  computed: {
    ...mapState({
      selectedTheme: (state) => state.themes.selectedTheme,
      selectedMode: (state) => state.themes.selectedMode,
      usedMode: (state) => state.themes.usedMode,
    }),
    active() {
      return (
        this.color === this.selectedTheme && this.usedMode === this.selectedMode
      );
    },
  },
  methods: {
    ...mapActions("themes", {
      setTheme: "set",
    }),
    changeTheme(color) {
      this.setTheme(color);
    },
  },
};
</script>

<style lang="scss" scoped>
.color-label {
  display: flex;
  align-items: center;
  margin-top: 1.6rem;
  transition: transform 50ms ease-out;

  &:active {
    transform: scale(0.98);
  }

  &__color {
    height: 1.2rem;
    width: 1.2rem;
    background-color: var(--color-prime);
    border-radius: 300px;
    border: 2px solid transparent;

    &.active {
      border: 2px solid var(--color-bg);
    }
  }

  &__title {
    margin-top: 1px;
    margin-left: 0.5rem;
    font-weight: 400;
    line-height: 1;
    text-transform: capitalize;
    font-size: 0.95rem;

    &.active {
      font-weight: 600;
      color: var(--color-bg);
    }
  }
}
</style>
