import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Streak from "../views/Streak.vue";
import StreakHistory from "../views/StreakHistory.vue";

import About from "../views/About.vue";
import Streaks from "../views/Streaks.vue";
import EditStreak from "../views/EditStreak.vue";
import AddStreak from "../views/AddStreak.vue";
import Stats from "../views/Stats.vue";
import History from "../views/History.vue";
import Settings from "../views/Settings.vue";
const DEFAULT_TRANSITION = "fade";

import store from "@/store/index.js";

Vue.use(VueRouter);

const routes = [
  // { path: "*", redirect: "/" },
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {
      transitionName: "slide",
      header: {
        title: "Home",
        back: false,
        menu: true,
      },
    },
  },
  {
    path: "/about",
    name: "About",
    component: About,
    meta: {
      transitionName: "fade",
      transitionLeaveName: "fade-top",
      hideBottomMenu: true,
      header: {
        title: "About",
        back: true,
        menu: false,
      },
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () =>
    //   import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  {
    path: "/streaks/:id",
    name: "Streaks",
    component: Streaks,
    meta: {
      transitionName: "fade",
      transitionLeaveName: "fade-top",
      header: {
        title: "Streaks",
        back: true,
        menu: false,
      },
    },
  },
  {
    path: "/streak/:id",
    name: "Streak",
    component: Streak,
    meta: {
      transitionName: "fade",
      transitionLeaveName: "fade-top",
      hideBottomMenu: true,

      header: {
        title: "Streak",
        back: true,
        menu: false,
      },
    },
  },
  {
    path: "/streak/history/:id",
    name: "StreakHistory",
    component: StreakHistory,
    meta: {
      transitionName: "fade",
      transitionLeaveName: "fade-top",
      hideBottomMenu: true,
      header: {
        title: "Streak > History",
        back: true,
        menu: false,
      },
    },
  },
  {
    path: "/editStreak/:id",
    name: "EditStreak",
    component: EditStreak,
    meta: {
      transitionName: "fade",
      transitionLeaveName: "fade-top",
      hideBottomMenu: true,
      header: {
        title: "Edit",
        back: true,
        menu: false,
      },
    },
  },
  {
    path: "/addStreak/",
    name: "AddStreak",
    component: AddStreak,
    meta: {
      transitionName: "fade",
      transitionLeaveName: "fade-top",
      hideBottomMenu: true,
      header: {
        title: "Add",
        back: true,
        menu: false,
      },
    },
  },
  {
    path: "/stats/",
    name: "Stats",
    component: Stats,
    meta: {
      transitionName: "slide",
      header: {
        title: "Stats",
        back: false,
        menu: true,
      },
    },
  },
  {
    path: "/history/",
    name: "History",
    component: History,
    meta: {
      transitionName: "slide",
      header: {
        title: "History",
        back: false,
        menu: true,
      },
    },
  },
  {
    path: "/settings/",
    name: "Settings",
    component: Settings,
    meta: {
      transitionName: "fade",
      transitionLeaveName: "fade-top",
      hideBottomMenu: true,
      header: {
        title: "Settings",
        back: true,
        menu: false,
      },
    },
  },
];

const router = new VueRouter({
  // base: "/test3",
  mode: "history",
  // base: process.env.BASE_URL,
  // scrollBehavior(to, from, savedPosition) {
  //   return new Promise((resolve, reject) => {
  //     setTimeout(() => {
  //       resolve({ x: 0, y: 0 });
  //     }, 200);
  //   });
  // },
  scrollBehavior(to, from, savedPosition) {
    return new Promise((resolve, reject) => {
      resolve({ x: 0, y: 0 });
    });
  },
  routes,
});

router.beforeEach((to, from, next) => {
  // catch sidebar routing (back btn close sidebar)
  if (store.state.sidebar.open) {
    store.dispatch("sidebar/close");
    return;
  } else {
    next();
  }

  //Set Header
  store.dispatch("setHeader", to.meta.header);
  //Get Settings
  let transitionName =
    from.meta.transitionLeaveName ||
    to.meta.transitionName ||
    from.meta.transitionName;
  const hideBottomMenu = to.meta.hideBottomMenu || false;
  //Hide/Show Bottom Menu
  if (hideBottomMenu) store.dispatch("bottomMenu/close");
  else if (!store.state.bottomMenu.open) store.dispatch("bottomMenu/open");

  //Set Slide direction
  if (transitionName === "slide") {
    const getPathIndex = store.getters["bottomMenu/pathIndex"];
    const toDepth = getPathIndex(to.path);
    const fromDepth = getPathIndex(from.path);

    transitionName = toDepth < fromDepth ? "slide-right" : "slide-left";
  }

  transitionName = transitionName || DEFAULT_TRANSITION;
  store.dispatch("setTransition", transitionName);
});

export default router;
