<template>
  <chartist ratio="ct-chart" type="Pie" :data="data" :options="options"></chartist>
</template>

<script>
import { size } from "@/components/mixins/main.js";

export default {
  name: "dc-circle",
  mixins: [size],
  beforeMount() {
    this.options.width = this.size;
    this.options.height = this.size;
    this.options.donutWidth = this.donutWidth;
  },
  data() {
    return {
      sizeS: 0.9,
      sizeM: 1,
      sizeL: 1.3,
      unit: "rem",
      donutWidth: "3px",

      data: {
        series: [this.progress, 100 - this.progress],
      },

      options: {
        showLabel: false,
        chartPadding: 0,
        donut: true,
      },
    };
  },
  props: {
    progress: {
      type: Number,
      required: true,
    },
    light: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    size() {
      const { small, large, sizeS, sizeL, sizeM } = this;
      const sizeStr = small ? sizeS : large ? sizeL : sizeM;
      return sizeStr + this.unit;
    },
  },
};
</script>

<style lang="scss" scoped>
.circle {
  transform: translateY(2px);
}
</style>
