<template>
  <div class="streak container">
    <!-- <div class="container-edit">
      <d-icon size="xs" icon="trash" @click.native="deleteStreak" class="trash-btn" />
      <d-icon size="xs" icon="edit" @click.native="goToEditStreak" />
    </div>-->

    <d-history :streak="streak" />

    <div class="btn-container">
      <d-button
        alt
        icon="check-circle"
        :disable="!showConfirmBtn"
        label="Check"
        @click.native="confirmCurrent"
      />
    </div>
  </div>
</template>

<script>
import dHistory from "@/components/dHistory.vue";
import { mapGetters, mapState } from "vuex";

export default {
  name: "Streak",
  components: {
    dHistory,
  },
  computed: {
    ...mapGetters("streaks", ["getStreakById"]),

    showConfirmBtn() {
      return this.streak.currentDate
        ? !this.streak.currentDate.isConfirmed
        : false;
    },
  },
  created() {
    this.streakId = this.$route.params.id;
    this.streak = this.getStreakById(this.streakId);

    this.title = this.streak.title;
  },
  data() {
    return {
      streak: null,
      streakId: null,
      title: "",
    };
  },
  methods: {
    confirmCurrent() {
      this.$store.dispatch("streaks/confirmCurrent", this.streakId);
      // this.$router.go(-1);
    },
    goToEditStreak() {
      this.$router.push(`/editStreak/${this.streakId}`);
    },
    deleteStreak() {
      this.$store.dispatch("streaks/delete", this.streakId);
      this.$router.go(-1);
    },
  },
};
</script>
<style lang="scss" scoped>
.streak {
  display: flex;
  flex-direction: column;
  flex-grow: 2;
}
.container-edit {
  margin-bottom: 2rem;
  display: flex;
  justify-content: flex-end;
}

.btn-container {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.trash-btn {
  margin-right: 1.7rem;
}
</style>
