export default {
  namespaced: true,
  state: {
    open: false,
  },
  mutations: {
    CLOSE(state) {
      state.open = false;
    },
    OPEN(state) {
      state.open = true;
    },
    TOGGLE(state) {
      state.open = !state.open;
    },
  },
  actions: {
    open({ commit }) {
      commit("OPEN");
    },
    close({ commit }) {
      commit("CLOSE");
      commit("menu/CLOSE", null, { root: true });
      commit("themes/CLOSE", null, { root: true });
    },
    toggle({ commit }) {
      commit("TOGGLE");
      commit("menu/CLOSE", null, { root: true });
      commit("themes/CLOSE", null, { root: true });
    },
  },
};
