import moment from "moment";

export const getRndNumber = function(max, min = 0) {
  return Math.floor(Math.random() * max) + min;
};

function isEven(mode, date) {
  const fixedMode = mode === "day" ? "date" : mode;

  let n = date.get(fixedMode);

  if (mode === "month") n++;

  return n % 2 === 0;
}

export const evenOrOdd = function(mode, date = moment()) {
  return isEven(mode, date) ? "even" : "odd";
};
