<template>
  <div class="theme-menu-container">
    <div class="theme-menu">
      <div class="header-icon">
        <d-icon icon="palette" />
        <p>Themes</p>
      </div>
      <div class="color-list">
        <transition-group name="list">
          <d-color-label
            v-for="color in colors[selectedMode]"
            :key="color + selectedMode"
            :color="color"
          />
        </transition-group>
      </div>
    </div>
    <span class="back-btn" @click="closeThemeMenu">
      <d-icon id="icon" icon="chevron-right" @click.native="closeThemeMenu" />
    </span>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import dColorLabel from "@/components/menu/dColorLabel.vue";

export default {
  name: "d-theme-menu",
  data() {
    return {};
  },
  components: {
    dColorLabel,
  },
  computed: {
    ...mapState({
      colors: (state) => state.themes.colors,
      selectedMode: (state) => state.themes.selectedMode,
    }),
  },
  methods: {
    ...mapActions("themes", {
      closeThemeMenu: "close",
    }),
  },
};
</script>

