import moment from "moment";

export default {
  namespaced: true,
  state: {
    historyArrays: [],
    historyArr: [],
    historyDay: [],
    historyWeek: [],
    historyMonth: [],
  },
  getters: {
    // historyArrays: (state, getters, rootState) => {
    //   const list = rootState.history.list;
    //   return Object.keys(list).map((key) => {
    //     const cList = list[key].filter((item) => item.isConfirmed);
    //     return {
    //       id: key,
    //       list: list[key],
    //       progress: (cList.length / list[key].length) * 100,
    //     };
    //   });
    // },
    // historyArr: (state, getters, rootState, rootGetters) => {
    //   const entries = [];
    //   getters.historyArrays.forEach((items) => {
    //     const { id, list } = items;
    //     list.forEach((item) => {
    //       const { date, isConfirmed } = item;
    //       const name = rootGetters["streaks/getStreakById"](id).title;
    //       entries.push({
    //         name,
    //         date,
    //         isConfirmed,
    //       });
    //     });
    //   });

    //   return entries.sort((a, b) => b.date - a.date);
    // },
    // // splitBy 'month' 'week' 'day' 'year'
    // history: (state, getters) => (splitBy) => {
    //   const list = getters.historyArr;
    //   const newList = [];
    //   let firstDate = list[0].date;
    //   let counter = 0;

    //   newList[counter] = {
    //     date: firstDate,
    //     items: [],
    //   };
    //   list.forEach((item) => {
    //     if (!firstDate.isSame(item.date, splitBy)) {
    //       counter++;
    //       firstDate = item.date;
    //       newList[counter] = {
    //         date: item.date,
    //         items: [],
    //       };
    //     }
    //     newList[counter].items.push({
    //       name: item.name,
    //       isConfirmed: item.isConfirmed,
    //     });
    //   });
    //   return newList;
    // },

    limited: (state, getters) => (limitDate, mode) => {
      return state.historyDay.filter((item) =>
        limitDate.isSameOrBefore(item.date)
      );
    },
    same: (state, getters) => (limitDate, same) => {
      return state.historyDay.filter((item) =>
        limitDate.isSame(item.date, same)
      );
    },
    last30Days: (state, getters) => {
      const limitDate = moment().subtract(1, "month");
      return getters.limited(limitDate, "day");
    },
    week: (state, getters) => (limitDate) => {
      return getters.same(limitDate, "week");
    },
  },

  mutations: {
    INIT_ARR(state, payload) {
      state.historyArr = payload;
    },
    INIT_ARRAYS(state, payload) {
      state.historyArrays = payload;
    },
    INIT_HISTORY_DAY(state, payload) {
      state.historyDay = payload;
    },
  },
  actions: {
    async init({ dispatch, commit, getters, rootState }) {
      await dispatch("initArrays");
      await dispatch("initArr");
      await dispatch("initHistoryDay");
    },

    async initArrays({ commit, rootState }) {
      const list = rootState.history.list;
      const historyArrays = Object.keys(list).map((key) => {
        const cList = list[key].filter((item) => item.isConfirmed);
        return {
          id: key,
          list: list[key],
          progress: (cList.length / list[key].length) * 100,
        };
      });

      commit("INIT_ARRAYS", historyArrays);
    },

    async initArr({ commit, state, rootGetters }) {
      const entries = [];
      state.historyArrays.forEach((items) => {
        const { id, list } = items;
        list.forEach((item) => {
          const { date, isConfirmed } = item;
          const name = rootGetters["streaks/getStreakById"](id).title;
          entries.push({
            name,
            date,
            isConfirmed,
          });
        });
      });

      const historyArr = entries.sort((a, b) => b.date - a.date);
      commit("INIT_ARR", historyArr);
    },
    async initHistoryDay({ commit, state }) {
      const splitBy = "day";
      const list = state.historyArr;
      const newList = [];
      let firstDate = list[0].date;
      let counter = 0;

      newList[counter] = {
        date: firstDate,
        items: [],
      };
      list.forEach((item) => {
        if (!firstDate.isSame(item.date, splitBy)) {
          counter++;
          firstDate = item.date;
          newList[counter] = {
            date: item.date,
            items: [],
          };
        }
        newList[counter].items.push({
          name: item.name,
          isConfirmed: item.isConfirmed,
        });
      });
      commit("INIT_HISTORY_DAY", newList);
    },
  },

  modules: {
    item: {
      namespaced: true,
      getters: {
        // progress: (state, getters, rootState, rootGetters) => (itemId) => {
        //   const list = rootState.history.list[itemId];
        //   const cList = list.filter((item) => item.isConfirmed);
        //   return Math.floor((cList.length / list.length) * 100);
        // },
        // upDown: (state, getters, rootState, rootGetters) => (itemId) => {
        //   const list = rootState.history.list[itemId].slice(
        //     0,
        //     rootState.history.list[itemId].length - 1
        //   );
        //   const cList = list.filter((item) => item.isConfirmed);
        //   const a = cList.length > 0 ? (cList.length / list.length) * 100 : 0;
        //   const b = getters.progress(itemId);
        //   return a === b ? 0 : a < b ? 1 : -1;
        // },
        progress: (state, getters, rootState, rootGetters) => (itemId) => {
          const list = rootState.history.list[itemId];
          const cList = list.filter((item) => item.isConfirmed);
          return Math.floor((cList.length / list.length) * 100);
        },
        upDown: (state, getters, rootState, rootGetters) => (itemId) => {
          const list = rootState.history.list[itemId].slice(
            0,
            rootState.history.list[itemId].length - 1
          );
          const cList = list.filter((item) => item.isConfirmed);
          const a = cList.length > 0 ? (cList.length / list.length) * 100 : 0;
          const b = getters.progress(itemId);
          return a === b ? 0 : a < b ? 1 : -1;
        },
      },
    },
    graphData: {
      namespaced: true,

      state: {
        chartOptions: {
          showArea: true,
          showLine: true,
          fullWidth: true,
          maintainAspectRatio: false,
          // lineSmooth: Interpolation.simple({
          //   divisor: 2,
          // }),
          axisX: {
            labelOffset: {
              x: -14,
              y: 3,
            },
          },
          axisY: {
            labelOffset: {
              x: -1,
              y: 5,
            },
            offset: 10,
          },
          chartPadding: {
            right: 25,
          },
        },
      },
      getters: {
        base: (state, getters) => (list) => {
          const dates = [];
          const posValues = [];
          const negValues = [];

          list.forEach((item) => {
            const { date, items } = item;
            const label = date.format("dd DD");
            const posValue = items.filter((item) => item.isConfirmed).length;
            const negValue = items.filter((item) => !item.isConfirmed).length;

            dates.unshift(label);
            posValues.unshift(posValue);
            negValues.unshift(-Math.abs(negValue));
          });

          return {
            chartData: {
              labels: dates,
              series: [posValues, negValues],
            },
            chartOptions: Object.assign(
              {
                high: Math.max(...posValues),
                low: Math.min(...negValues),
              },
              state.chartOptions
            ),
          };
        },
        last30Days: (state, getters, rootState, rootGetters) => {
          const limitDate = moment().subtract(1, "month");
          const list = rootGetters["stats/limited"](limitDate, "day");
          return getters.base(list);
        },
        lastYear: (state, getters, rootState, rootGetters) => {
          const limitDate = moment().subtract(1, "year");
          const list = rootGetters["stats/limited"](limitDate, "month");
          return getters.base(list);
        },
        last7Days: (state, getters, rootState, rootGetters) => {
          const limitDate = moment().subtract(8, "days");
          const list = rootGetters["stats/limited"](limitDate, "day");
          return getters.base(list);
        },
        week: (state, getters, rootState, rootGetters) => (date) => {
          const list = rootGetters["stats/same"](date, "week");
          return getters.base(list);
        },
      },
    },

    progress: {
      namespaced: true,

      getters: {
        base: (state, getters) => (list) => {
          let sumPos = 0;
          let sumNeg = 0;

          list.forEach((item) => {
            sumPos += item.items.filter((item) => item.isConfirmed).length;
            sumNeg += item.items.filter((item) => !item.isConfirmed).length;
          });

          const total = sumPos + sumNeg;
          return {
            total,
            perc: Math.round((sumPos / total) * 100),
            done: sumPos,
            missed: sumNeg,
          };
        },

        thisMonth: (state, getters, rootState, rootGetters) => {
          const limitDate = moment().startOf("month");
          const list = rootGetters["stats/limited"](limitDate, "day");
          return getters.base(list);
        },
        thisYear: (state, getters, rootState, rootGetters) => {
          const limitDate = moment().startOf("year");
          const list = rootGetters["stats/limited"](limitDate, "month");
          return getters.base(list);
        },
        thisWeek: (state, getters, rootState, rootGetters) => {
          const limitDate = moment().startOf("week");
          const list = rootGetters["stats/limited"](limitDate, "day");
          return getters.base(list);
        },
        last7Days: (state, getters, rootState, rootGetters) => {
          const limitDate = moment().subtract(8, "days");
          const list = rootGetters["stats/limited"](limitDate, "day");
          return getters.base(list);
        },
        week: (state, getters, rootState, rootGetters) => (date) => {
          const list = rootGetters["stats/same"](date, "week");
          return getters.base(list);
        },
        top5: (state, getters, rootState, rootGetters) => {
          return rootState.stats.historyArrays
            .sort((a, b) => b.progress - a.progress)
            .slice(0, 5)
            .map((item) => rootGetters["streaks/getStreakById"](item.id));
        },
        bottom5: (state, getters, rootState, rootGetters) => {
          return rootState.stats.historyArrays
            .sort((a, b) => a.progress - b.progress)
            .slice(0, 5)
            .map((item) => rootGetters["streaks/getStreakById"](item.id));
        },
      },
    },
  },
};
