<template>
  <div class="progress-card">
    <div class="progress-card__left">
      <div class="progress-card__label">
        <p>{{title}}</p>
        <p class="date" sub v-if="title.includes('Week')">{{$moment().format('[WK] w')}}</p>
        <p class="date" sub v-else-if="title.includes('Month')">{{$moment().format('MMMM')}}</p>
        <p class="date" sub v-else-if="title.includes('Year')">{{$moment().format('GGGG') }}</p>
      </div>
      <dc-progress-circle large :progress="progress.perc" />
    </div>

    <div class="progress-card__right">
      <div class="progress-card__stats">
        <dc-progress small check :progress="progress.done" />
        <dc-progress small times :progress="progress.missed" />
      </div>
    </div>
  </div>
</template>

<script>
import dcProgress from "@/components/charts/dcProgress.vue";
import dcProgressCircle from "@/components/charts/dcProgressCircle.vue";

export default {
  name: "dc-progress-card",
  components: {
    dcProgress,
    dcProgressCircle,
  },
  props: {
    title: {
      type: String,
      default: "",
    },
    progress: {
      type: Object,
      required: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.progress-card {
  @include card;

  padding-right: 1rem;
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  padding-left: 0.5rem;
  height: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-right: 0.4rem;
  }

  &__label {
    margin-bottom: 1rem;
    font-size: 0.85rem;
  }
  &__stats {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    height: 100%;
    & > :first-child {
      margin-bottom: 0.3rem;
    }
  }
}

.date {
  font-size: 0.7rem;
  line-height: 0.8;
  font-weight: 500;
  text-align: center;

  border-radius: 3px;
  min-height: 0.75rem;
  min-width: 0.75rem;
  overflow: hidden;

  padding: 1.5px 4px 1.5px 4px;
  margin-top: 2px;
  margin-left: -1px;

  background-color: var(--color-bg-light);
  color: var(--color-prime-light);
}
</style>