export default {
  namespaced: true,
  state: {
    open: true,
    items: [
      { icon: "calendar-check", path: "/" },
      { icon: "chart-area", path: "/stats" },
      { icon: "history", path: "/history" },
    ],
  },
  getters: {
    pathIndex: (state) => (path) => {
      return state.items.findIndex((item) => item.path === path);
    },
  },
  mutations: {
    TOGGLE(state) {
      state.open = !state.open;
    },
    CLOSE(state) {
      state.open = false;
    },
    OPEN(state) {
      state.open = true;
    },
  },
  actions: {
    toggle({ commit }) {
      commit("TOGGLE");
    },
    close({ commit }) {
      commit("CLOSE");
    },
    open({ commit }) {
      commit("OPEN");
    },
  },
};
