<template>
  <div class="container">
    <div>
      <d-edit-streak v-model="streak" :streakEdit="streakToEdit" />
    </div>
    <div>
      <d-button label="Save" @click.native="editStreak" />
    </div>
  </div>
</template>

<script>
import dEditStreak from "@/components/dEditStreak.vue";
import { mapGetters } from "vuex";

export default {
  name: "EditStreak",
  components: {
    dEditStreak,
  },
  created() {
    const streakId = this.$route.params.id;
    this.streakToEdit = this.getStreakById(streakId);
  },
  computed: {
    ...mapGetters("streaks", ["getStreakById"]),
  },

  data() {
    return {
      streak: {},
      streakToEdit: {},
    };
  },
  methods: {
    editStreak() {
      this.$store.dispatch("streaks/edit", this.streak.streak);

      this.$router.push("/");
    },
  },
};
</script>
