<template>
  <div class="container">
    <div>
      <d-edit-streak v-model="streak" />
    </div>
    <div>
      <d-button label="Add" @click.native="addStreak" />
    </div>
  </div>
</template>

<script>
import dEditStreak from "@/components/dEditStreak.vue";
export default {
  name: "AddStreak",
  components: {
    dEditStreak,
  },
  data() {
    return {
      streak: {},
    };
  },
  methods: {
    addStreak() {
      this.$store.dispatch("streaks/add", this.streak.streak);

      this.$router.push("/");
    },
  },
};
</script>
