<template>
  <div class="input-container">
    <input
      class="input"
      :type="type"
      placeholder="x"
      :value="value"
      ref="userInput"
      :id="uId"
      @input="update()"
    />
    <label class="label" :for="uId">
      <span>
        {{ label }}
        <!-- <d-icon icon="meteor" class="fa-flip-vertical fa-flip-horizontal" /> -->
      </span>
    </label>
  </div>
</template>

<script>
export default {
  name: "d-input",
  created() {
    this.uId = this.$uniqid.process();
  },
  data() {
    return {
      uId: "",
      checked: false,
    };
  },
  props: {
    value: {
      type: String,
      default: "",
    },
    label: {
      type: String,
      default: "label",
    },
    type: {
      type: String,
      default: "text",
    },
  },
  methods: {
    update() {
      this.$emit("input", this.$refs.userInput.value);
    },
  },
};
</script>

<style lang="scss" scoped>
input {
  // @include box;
  width: 100%;
  padding-top: 1.2rem;
  padding-bottom: 0.2rem;
  padding-left: 0.1rem;
  font-size: 1.3rem;
  color: var(--color-muted);
  border-bottom: 2px dotted var(--color-prime);
}
input:focus {
  // border-bottom: 2px solid var(--color-bg-light);
  // border-bottom: 2px solid var(--color-prime);
}

.input-container {
  position: relative;
}

label {
  position: absolute;
  top: 0.9rem;
  left: 0.1rem;
  font-size: 1.2rem;
  transform: translateY(0.2rem);
  font-weight: 500;
  color: var(--color-prime);
  transition: all 150ms linear;
}
input::placeholder {
  visibility: hidden;
}

input:focus + label,
input:not(:placeholder-shown) + label {
  transform: translateY(-0.5rem);
  font-size: 0.8rem;
  color: var(--color-prime);
}
input:not(:placeholder-shown) {
  border-bottom: 2px dotted var(--color-prime);
}
</style>
