<template>
  <div class="edit-container">
    <div class="mode">
      <d-icon class="mode__icon infinity active" icon="infinity" />
      <d-icon class="mode__icon trophy" icon="trophy" />
      <d-icon class="mode__icon" icon="stopwatch" />
    </div>
    <d-input label="Title" class="title" v-model="streak.title" />
    <d-tag-picker :tags="option" v-model="streak.option" class="d-tag-picker" />
    <d-tag-picker
      :active="!!streak.option.length"
      :tags="mode"
      v-model="streak.mode"
      class="d-tag-picker"
      :needsAnS="needsAnS"
    />
    <transition name="fade">
      <d-tag-picker
        v-if="streak.option === 'some'"
        :tags="someList"
        multi
        v-model="streak.some"
        class="d-tag-picker"
      />
    </transition>
  </div>
</template>

<script>
import dTagPicker from "@/components/dTagPicker.vue";
import { mapState } from "vuex";

export default {
  name: "d-edit-streak",
  components: {
    dTagPicker,
  },
  computed: {
    ...mapState({
      days: (state) => state.repeats.dayShort,
      weeks: (state) => state.repeats.weeks,
      months: (state) => state.repeats.monthShort,
      mode: (state) => state.repeats.mode,
      option: (state) => state.repeats.option,
    }),
    someList() {
      return this.streak.mode === "day"
        ? this.days
        : this.streak.mode === "week"
        ? this.weeks
        : this.streak.mode === "month"
        ? this.months
        : [];
    },
    needsAnS() {
      return this.streak.option !== "every";
    },
  },
  data() {
    return {
      streak: {
        title: "",
        some: [],
        mode: "",
        option: "",
      },
    };
  },
  watch: {
    streak: {
      deep: true,
      handler(newValue) {
        this.$emit("input", newValue);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.edit-container {
}

.mode {
  display: flex;
  gap: 0 1.4rem;
  margin-bottom: 1rem;

  &__icon {
    color: var(--color-prime-light);
    color: var(--color-muted);

    &.infinity {
      transform: scale(1.1);
    }

    &.trophy {
      transform: scale(0.9);
    }

    &.active {
      color: var(--color-prime);
    }
  }
}

.title {
  margin-bottom: 2.5rem;
}

.d-tag-picker {
  margin-bottom: 1.2rem;
}
</style>
