export const setLocal = function(data, path) {
  localStorage.setItem(path, JSON.stringify(data));
};

export const getLocal = function(path) {
  return JSON.parse(localStorage.getItem(path));
};

const deleayTime = 500;

export const fetchLocalData = async (path) => {
  await new Promise((resolve) => setTimeout(resolve, deleayTime));
  return getLocal(path);
};
