<template>
  <div :class="['tag']">
    <p
      v-for="txt in text.split(',')"
      :key="txt"
      :class="{ selected: isSelected(txt) }"
    >{{ txt.trim() }}</p>
  </div>
</template>

<script>
export default {
  name: "d-tag",
  props: {
    tag: {
      type: Object,
      required: true,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    item: {
      type: Object,
      required: true,
    },
  },
  computed: {
    text() {
      const { option, mode, some } = this.tag;
      if (["odd", "even"].includes(option)) return `${option} ${mode}s`;
      if (option === "every") return mode === "day" ? "daily" : mode + "ly";
      if (option === "some") return this.toDayString(some);

      return [];
    },
  },
  methods: {
    toDayString(days) {
      return days.map((d) => this.$moment().day(d).format("dd")).toString();
    },
    isSelected(txt) {
      return this.tag.option === "some"
        ? this.$moment().format("dd") === txt
        : this.item.currentDate || false;
    },
  },
};
</script>

<style lang="scss" scoped>
.tag {
  text-transform: lowercase;
  font-size: 0.75rem;
  font-weight: 500;
  overflow: hidden;
  display: flex;
  // align-items: center;
  // flex-wrap: wrap;

  & p {
    text-align: center;
    min-width: 0.75rem;
    min-height: 0.75rem;
    border-radius: 5px;
    line-height: 0.8;
    padding-top: 2px;
    &:not(:last-of-type) {
      margin-right: 0.2rem;
    }
    &.selected {
      padding: 1.5px 4px 2px 4px;
      margin-left: -1px;

      background-color: var(--color-bg-light);
      color: var(--color-prime);
    }
  }
}
</style>
