import moment from "moment";

export const timetravel = {
  isSameDay: (cDate) => moment().isSame(cDate, "day"),
  isYesterday: (cDate) =>
    moment()
      .subtract(1, "days")
      .isSame(cDate, "day"),
  isNextDay: (cDate) =>
    moment()
      .add(1, "days")
      .isSame(cDate, "day"),
  isSameWeek: (cDate) => moment().isSame(cDate, "Week"),
  isLastWeek: (cDate) =>
    moment()
      .subtract(1, "week")
      .isSame(cDate, "week"),
  isNextWeek: (cDate) =>
    moment()
      .add(1, "week")
      .isSame(cDate, "week"),
  isSameMonth: (cDate) => moment().isSame(cDate, "Month"),
  isLastMonth: (cDate) =>
    moment()
      .subtract(1, "month")
      .isSame(cDate, "month"),
  isNextMonth: (cDate) =>
    moment()
      .add(1, "month")
      .isSame(cDate, "month"),
  isLastDayOfMonth: () =>
    moment()
      .endOf("month")
      .isSame(moment(), "day"),
};
