import uniqid from "uniqid";
import moment from "moment";

import { evenOrOdd, getRndNumber } from "@/helper/helper.js";

export default {
  namespaced: true,
  state: {
    list: {},
  },
  getters: {
    getNextDate: (state, getters) => (item, d = moment()) => {
      return getters.getDate(item, d, "next");
    },

    getPrevDate: (state, getters) => (item, d = moment()) => {
      return getters.getDate(item, d, "prev");
    },
    getCurrentDate: (state, getters) => (item) => {
      const { isConfirmed, confirmId } = getters.isConfirmedCurr(item);

      return {
        isConfirmed,
        confirmId,
        date: moment(),
        id: uniqid(),
      };
    },
    getDate: (state, getters) => (item, d = moment(), calcMode = "prev") => {
      const date = moment(d);
      const subtractOrAdd = calcMode === "prev" ? "subtract" : "add";
      const mode = item.period[0].mode + "s";

      do {
        date[subtractOrAdd](1, mode);
      } while (!getters.isCurrent(item, date));
      const { isConfirmed, confirmId } = getters.isConfirmed(
        item,
        moment(date)
      );
      return {
        isConfirmed,
        confirmId,
        date,
        id: uniqid(),
      };
    },
    isConfirmed: (state, getters) => (item, date = moment()) => {
      const { mode, confirms } = item.period[0];
      const isCurrent = moment().isSameOrBefore(date, "day");
      let hasConfirmed = {
        isConfirmed: isCurrent ? false : getRndNumber(100) < 60 ? true : false,
        confirmId: "",
      };
      // let hasConfirmed = {
      //   isConfirmed: false,
      //   confirmId: "",
      // };
      // confirms.forEach((confirm) => {
      //   if (moment(confirm.date).isSame(date, mode))
      //     hasConfirmed = {
      //       isConfirmed: true,
      //       confirmId: confirm.id,
      //     };
      // });

      return hasConfirmed;
    },
    isConfirmedCurr: (state, getters) => (item, date = moment()) => {
      const { mode, confirms } = item.period[0];

      let hasConfirmed = {
        isConfirmed: false,
        confirmId: "",
      };
      confirms.forEach((confirm) => {
        if (moment(confirm.date).isSame(date, mode))
          hasConfirmed = {
            isConfirmed: true,
            confirmId: confirm.id,
          };
      });

      return hasConfirmed;
    },
    isCurrent: () => (item, date = moment()) => {
      const { option, some, mode } = item.period[0];

      // if daily/monthly/weekly
      if (option === "every") return true;
      // if odd / even option current day/month/week
      if (["even", "odd"].includes(option))
        return evenOrOdd(mode, date) === option;
      // if some include  - current day/month/week
      if (option === "some") return some.includes(date.get(mode));

      return false;
    },
  },

  mutations: {
    SET_HISTORY_LIST(state, historyList) {
      state.list = historyList;
    },
    CONFIRM(state, { streakId, itemId, confirmId }) {
      const item = state.list[streakId].find((item) => item.id === itemId);
      item.currentDate.isConfirmed = true;
      item.currentDate.confirmId = confirmId;
    },
  },
  actions: {
    confirm({ commit }, { streakId, item }) {
      const confirmDate = { date: item.date, id: uniqid() };
      commit("streaks/ADD_CONFIRM", { streakId, confirmDate }, { root: true });
      commit("CONFIRM", {
        streakId,
        itemId: item.id,
        confirmId: confirmDate.id,
      });
    },
    init({ commit, getters, rootState }) {
      const items = rootState.streaks.list;
      const historyList = {};

      items.forEach((item) => {
        let prevDate = getters.getCurrentDate(item);

        const { from } = item.period[0];
        const list = [];

        do {
          prevDate = getters.getPrevDate(item, prevDate.date);
          list.unshift(prevDate);
        } while (moment(from).isSameOrBefore(prevDate.date));

        historyList[item.id] = list;
      });

      commit("SET_HISTORY_LIST", historyList);
    },
  },
  modules: {},
};
