import uniqid from "uniqid";
import moment from "moment";

import fakeApi from "@/data/streaks.js";
import { setLocal } from "@/plugins/SaveLocal.js";
import { evenOrOdd } from "@/helper/helper.js";

export default {
  namespaced: true,
  state: {
    list: [],
    localPath: "streaks",
    fetching: false,
    filter: {
      mode: {
        day: (item) => item.period[0].mode === "day",
        week: (item) => item.period[0].mode === "week",
        month: (item) => item.period[0].mode === "month",
      },
      option: {
        every: (item) => item.period[0].option === "every",
        even: (item) => item.period[0].option === "even",
        odd: (item) => item.period[0].option === "odd",
        some: (item) =>
          item.period[0].some.includes(moment().get(item.period[0].mode)),
      },
    },
  },
  getters: {
    getStreakById: (state) => (id) => {
      return state.list.find((item) => item.id === id);
    },
    getByMode: (state, getters) => (mode) => {
      return state.list.filter(state.filter.mode[mode]);
    },
    isItemConfirmed: (state) => (item) => {
      return item.currentDate ? item.currentDate.isConfirmed : false;
    },

    filterOptions: (state, getters) => (mode) => {
      const list = getters.getByMode(mode);

      //Get filter
      const evenOrOddFilter = state.filter.option[evenOrOdd(mode)];
      const { every, some } = state.filter.option;

      //filter options
      return list.filter(
        (item) => some(item) || evenOrOddFilter(item) || every(item)
      );
    },

    today: (state, getters) => {
      return getters.filterOptions("day");
    },
    week: (state, getters) => {
      return getters.filterOptions("week");
    },
    month: (state, getters) => {
      return getters.filterOptions("month");
    },
  },
  mutations: {
    ADD(state, streak) {
      state.list = [streak, ...state.list];
    },
    EDIT(state, streak) {
      state.list = [
        streak,
        ...state.list.filter((item) => item.id !== streak.id),
      ];
    },
    DELETE(state, streakId) {
      state.list = state.list.filter((item) => item.id !== streakId);
    },
    ADD_LIST(state, streaks) {
      state.list = [...streaks];
    },
    ADD_CONFIRM(state, { streakId, confirmDate }) {
      const streak = state.list.find((item) => item.id === streakId);
      const currPeriod = streak.period[0];
      if (!currPeriod.confirms) {
        currPeriod.confirms = [];
      }
      currPeriod.confirms.push(confirmDate);
    },
    SET_CURRENT_DATE(state, { streakId, currentDate }) {
      const streak = state.list.find((streak) => streak.id === streakId);
      streak.currentDate = currentDate;
    },

    UPDATE_LOCAL_STORE(state) {
      setLocal(state.list, state.localPath);
    },
  },
  actions: {
    confirmCurrent({ commit, getters, rootGetters }, streakId) {
      const item = getters.getStreakById(streakId);
      const isItemCurrent = rootGetters["history/isCurrent"](item);
      if (isItemCurrent) {
        const confirmDate = { date: moment(), id: uniqid() };
        commit("ADD_CONFIRM", { streakId, confirmDate });

        const currentDate = rootGetters["history/getCurrentDate"](item);
        commit("SET_CURRENT_DATE", { streakId, currentDate });
      }
    },

    add({ dispatch, commit, rootGetters }, streak) {
      if (streak.option === "some") {
        streak.some = rootGetters["repeats/someToInt"](
          streak.some,
          streak.mode
        );
      }
      const newStreak = {
        createdAt: moment(),
        id: uniqid(),
        period: [
          {
            confirms: [],
            from: moment(),
            mode: streak.mode,
            option: streak.option,
            some: streak.some,
            to: null,
          },
        ],
        title: streak.title,
      };
      if (rootGetters["history/isCurrent"](newStreak)) {
        newStreak.currentDate = rootGetters["history/getCurrentDate"](
          newStreak
        );
        newStreak.nextDate = rootGetters["history/getNextDate"](newStreak);
      }
      commit("ADD", newStreak);
      dispatch("history/init", null, { root: true });

      commit("UPDATE_LOCAL_STORE");
    },
    edit({ commit }, payload) {
      commit("EDIT", payload);
      commit("UPDATE_LOCAL_STORE");
    },
    delete({ commit }, payload) {
      commit("DELETE", payload);
      commit("UPDATE_LOCAL_STORE");
    },
    async fetchPosts({ dispatch, commit, state, rootGetters }) {
      state.fetching = true;
      // let streaks = await fetchLocalData(state.localPath);

      const streaks = await fakeApi.fetchData();
      streaks.forEach((streak) => {
        if (rootGetters["history/isCurrent"](streak)) {
          streak.currentDate = rootGetters["history/getCurrentDate"](streak);
          streak.nextDate = rootGetters["history/getNextDate"](streak);
        }
      });

      if (streaks) {
        await commit("ADD_LIST", streaks);
        await dispatch("history/init", null, { root: true });
        await dispatch("stats/init", null, { root: true });
        await dispatch("initProgress");
      }
      state.fetching = false;
    },
    initProgress({ state, rootGetters }) {
      state.list.forEach(
        (item) => (item.progress = rootGetters["stats/item/progress"](item.id))
      );
    },
  },
  modules: {},
};
