<template>
  <div v-if="open" class="bottom-menu">
    <div class="bottom-menu__items">
      <d-menu-item alt v-for="item in items" :key="item.path" :item="item" />
    </div>
  </div>
</template>

<script>
import dMenuItem from "@/components/menu/dMenuItem.vue";
import { mapState } from "vuex";

export default {
  name: "d-bottom-menu",
  components: {
    dMenuItem,
  },
  computed: {
    ...mapState({
      items: (state) => state.bottomMenu.items,
      addItem: (state) => state.bottomMenu.addItem,
      open: (state) => state.bottomMenu.open,
    }),
  },
};
</script>

<style lang="scss" scoped>
.bottom-menu {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-bottom: 1.5rem;
  padding-top: 1.58rem;
  padding-right: 4rem;
  background: linear-gradient(
    rgba($color: #000000, $alpha: 0) 0%,
    var(--color-bg) 75%
  );

  &__items {
    opacity: 1;
    display: flex;
    justify-content: space-around;
    flex-grow: 1;
  }
}
</style>