<template>
  <div :class="[color, 'snackbar']" v-if="show" :color="color">
    {{ message }}
    <!-- <btn text @click="show = false">Close</btn> -->
  </div>
</template>

<script>
export default {
  name: "d-snackbar",
  data() {
    return {
      show: false,
      message: "",
      color: "",
      id: "",
    };
  },

  created() {
    this.$store.subscribe((mutation, state) => {
      if (mutation.type === "snackbar/showMessage") {
        this.message = state.snackbar.content;
        this.color = state.snackbar.color;
        this.show = true;

        this.id = this.$uniqid();
        const id = this.id;

        setTimeout(() => {
          if (this.id === id) {
            this.show = false;
          }
        }, state.snackbar.showDuration);
      }
    });
    // this.$router.afterEach((to, from) => {
    //   this.show = false;
    // });
  },
};
</script>

<style lang="scss" scoped>
.snackbar {
  padding: 9px 1rem;
  color: var(--color-darken);
  border-radius: 3px;
  font-weight: 600;
  // box-shadow: var(--shadow);

  &.ok {
    background-color: var(--color-ok);
  }
  &.danger {
    background-color: var(--color-danger);
  }
  &.warning {
    background-color: var(--color-warning);
  }
}

.snackbar {
  position: fixed;
  bottom: 1.5rem;
  margin: 0 2rem;
  width: calc(100% - 4rem);
}
</style>
