<template>
  <div class="dc-chart">
    <div class="dc-chart__header">
      <div class="dc-chart__header__labels">
        <d-label :icon="icon">{{title}}</d-label>
        <d-label sub>{{subTitle}}</d-label>
      </div>

      <div class="dc-chart__header__stats">
        <dc-progress check :progress="progress.done" />
        <dc-progress times :progress="progress.missed" />
      </div>

      <div class="dc-chart__header__circle">
        <dc-progress-circle :progress="progress.perc" />
      </div>
    </div>

    <chartist
      ratio="ct-chart"
      type="Line"
      :data="graphData.chartData"
      :options="graphData.chartOptions"
    ></chartist>
  </div>
</template>

<script>
import dcProgressCircle from "@/components/charts/dcProgressCircle.vue";
import dcProgress from "@/components/charts/dcProgress.vue";

export default {
  name: "dc-chart-base",
  components: {
    dcProgressCircle,
    dcProgress,
  },
  props: {
    graphData: {
      type: Object,
      requried: true,
    },
    progress: {
      type: Object,
      requried: true,
    },
    title: {
      type: String,
      requried: true,
    },
    subTitle: {
      type: String,
      requried: true,
    },
    icon: {
      type: String,
      requried: false,
      default: "",
    },
  },
};
</script>

<style lang="scss" scoped>
.dc-chart {
  margin-left: 0.9rem;

  &__header {
    margin-bottom: 0.3rem;
    padding-right: 1.5rem;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: flex-end;

    &__stats {
      display: flex;
      align-items: flex-end;

      & > :first-child {
        margin-right: 1rem;
      }
    }
    &__circle {
      align-items: flex-end;
    }
  }
}
</style>
