<template>
  <div class="period" v-if="list">
    <span class="list-style-ctrl" @click="listStyle = !listStyle">
      <d-icon :icon="listStyle ? 'square' : 'list-alt'" :prefix="'far'" />
    </span>
    <div @click="routeTo" class="period__date">
      <d-label small :icon="icon" :content="content" />
      <d-label sub v-if="content === 'today'">
        {{
        $moment().format("dd Do")
        }}
      </d-label>
      <d-label sub v-else-if="content === 'all'">
        {{
        $moment().format("MM.DD.GG")
        }}
      </d-label>
      <d-label sub v-else-if="content === 'week'">
        {{
        $moment().format("[WK] w")
        }}
      </d-label>
      <d-label sub v-else-if="content === 'month'">
        {{
        $moment().format("MMMM")
        }}
      </d-label>
    </div>

    <transition-group name="streak-list" :class="['period__items', { listStyle }]" tag="div">
      <d-card v-for="streak in list" :key="streak.id" :streak="streak" :list="listStyle" />
    </transition-group>
  </div>
</template>

<script>
import { route } from "@/components/mixins/main.js";

export default {
  name: "d-streak-list",
  mixins: [route],
  data() {
    return {
      listStyle: true,
    };
  },
  beforeMount() {
    if (this.content === "week") {
      this.listStyle = false;
    }
  },
  props: {
    list: {
      type: Array,
      required: true,
    },
    content: {
      type: String,
      default: "",
    },
    icon: {
      type: String,
      default: "",
    },
  },
};
</script>

<style lang="scss" scoped>
.list-style-ctrl {
  position: absolute;
  top: -0.6rem;
  right: 1.8rem;
  color: var(--color-muted);
}

.period {
  position: relative;
  padding: 1.5rem 1.5rem 1rem 0.9rem;
  display: flex;

  &:nth-child(odd) {
    padding-top: 3.3rem;
    padding-bottom: 3.5rem;
    clip-path: polygon(
      0 calc(0% + 1.7rem),
      100% 0,
      100% calc(100% - 1.8rem),
      0 100%
    );
    background-color: var(--color-bg-lighten);

    .list-style-ctrl {
      position: absolute;
      top: 1.3rem;
    }
  }

  &__date {
    width: 4.4rem;
    padding-top: 0.15rem;
    border-right: 1.5px dashed var(--color-muted-alpha);
    margin-right: 0.4rem;
  }
  &__items {
    flex-grow: 1;

    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(6rem, 1fr));

    &.listStyle {
      grid-template-columns: 1fr;
      row-gap: 6px;
    }

    & > * {
      transition: transform 400ms cubic-bezier(0.55, 0, 0.1, 1),
        opacity 400ms cubic-bezier(0.55, 0, 0.1, 1);
    }
  }
}
</style>
