<template>
  <div :class="['item-container', {bottom}]">
    <d-label class="header" xSmall>{{this.bottom ? "Bottom" : "Top" }} 5</d-label>

    <div :class="['item', {bottom}]" v-for="item in list" :key="item.id">
      <dc-up-down :id="item.id" />
      <d-label xSmall>{{item.title}}</d-label>
      <dc-progress small perc :progress="progress(item.id)" />
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import dcProgress from "@/components/charts/dcProgress.vue";
import dcUpDown from "@/components/charts/dcUpDown.vue";

export default {
  name: "dc-top-bottom-five",
  components: {
    dcProgress,
    dcUpDown,
  },
  mounted() {
    const getter = this.bottom ? "bottom" : "top";
    this.list = this.$store.getters[`stats/progress/${getter}5`];
  },
  props: {
    bottom: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapGetters({ progress: "stats/item/progress" }),
  },
  data() {
    return {
      list: null,
    };
  },
};
</script>

<style lang="scss" scoped>
.item-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 0.3rem;
  background-color: var(--color-bg-alpha);
  padding: 0.8rem 0.9rem;
  padding-bottom: 0.9rem;
  border-radius: 6px;
  box-shadow: 5px 4px 9px -4px var(--color-shadow),
    -7px -6px 9px -4px var(--color-shadow);
}
.header {
  // border-left: 2px solid var(--color-prime);
  // padding-left: 0.4rem;
  margin-bottom: 0.3rem;
}
.bottom .header {
  // border-left: 2px solid var(--color-danger);
}

.item {
  overflow: hidden;
  position: relative;

  display: flex;
  justify-content: space-between;
  color: var(--color-prime);
  padding: 0.2rem 0.1rem;
  .title {
    display: flex;
    gap: 0.4rem;
    align-items: baseline;
    // padding-bottom: px;
  }
  &:not(:last-of-type) {
    border-bottom: 2px dotted var(--color-border);
  }

  & .progress-slide {
    position: absolute;
    bottom: 0px;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: var(--color-prime-light);
    z-index: -1;

    // border-radius: 0 4px 4px 0;
  }
  &.bottom {
    color: var(--color-danger);
    & .progress-slide {
      background-color: var(--color-danger-light);
    }
  }
}
</style>