<template>
  <div class="history">
    <div class="history__label">
      <d-label class="list-label" content="HISTORY" @click.native="routeHistory" />
      <d-tag :item="streak" :tag="streak.period[0]" />
    </div>
    <div class="history__header">
      <div class="history__header__item">Latest</div>
      <div class="history__header__item">Current</div>
      <div class="history__header__item">Next</div>
    </div>
    <div class="history__list" v-if="historyShort">
      <d-confirm
        v-for="date in historyShort"
        :key="date.id"
        :confirm="date"
        :streakId="streakId"
        :mode="streak.period[0].mode"
        history
      ></d-confirm>
      <d-confirm
        v-if="streak.currentDate"
        @confirmCurrent="confirmCurrent"
        current
        :confirm="streak.currentDate"
        :streakId="streakId"
        :mode="streak.period[0].mode"
      ></d-confirm>
      <d-confirm
        v-if="nextDate"
        :confirm="nextDate"
        :streakId="streakId"
        :mode="streak.period[0].mode"
      ></d-confirm>
    </div>
  </div>
</template>

<script>
import dConfirm from "@/components/dConfirm.vue";
import { mapGetters } from "vuex";

export default {
  name: "d-history",
  components: {
    dConfirm,
  },
  computed: {
    ...mapGetters("history", ["getNextDate"]),
    historyShort() {
      return this.history
        ? this.history.slice(this.history.length - 3, this.history.length)
        : null;
    },
  },
  created() {
    this.streakId = this.streak.id;
    this.nextDate = this.streak.nextDate;
    this.history = this.$store.state.history.list[this.streakId];
  },
  data() {
    return {
      streakId: null,
      nextDate: null,
      next: null,
      history: null,
    };
  },
  methods: {
    confirmCurrent() {
      this.$store.dispatch("streaks/confirmCurrent", this.streakId);
    },
    routeHistory() {
      this.$router.push(`/streak/history/${this.streakId}`);
    },
  },
  props: {
    streak: {
      type: Object,
      required: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.history {
  &__header {
    gap: 6px;
    margin-bottom: 6px;
    text-align: center;
    font-size: 0.8rem;
    color: var(--color-text);
    display: grid;
    grid-template-columns: 3.04fr 0.98fr 0.98fr;
    font-weight: normal;
    &__item {
      background-color: var(--color-bg-alpha);
      padding: 2px 0;
      border-radius: 2px;
    }
  }

  &__label {
    font-size: var(--font-size-small);
    font-weight: lighter;
    border-left: 2px solid var(--color-prime);
    padding-left: 0.4rem;
    margin-bottom: 1rem;
  }

  &__list {
    gap: 5px;

    grid-template-columns: repeat(auto-fit, minmax(2rem, 1fr));
    display: grid;
  }
}
</style>