<template>
  <div class="container-add" v-if="isSidebarOpen">
    <!-- <div class="container-dark line"></div> -->
    <div class="container-darks">
      <d-edit-streak v-model="streak" />
    </div>
    <div class="ctrl-container">
      <d-button alt class="add-button" label="Add" @click.native="addStreak" icon="plus-circle" />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

import dEditStreak from "@/components/dEditStreak.vue";
export default {
  name: "d-add",
  components: {
    dEditStreak,
  },
  data() {
    return {
      streak: {},
    };
  },
  methods: {
    addStreak() {
      this.$store.dispatch("streaks/add", this.streak);
      this.$store.dispatch("sidebar/close");
      this.streak = {};
    },
  },
  computed: {
    ...mapState({
      isSidebarOpen: (state) => state.sidebar.open,
    }),
  },
};
</script>

<style lang="scss" scoped>
.container-add {
  display: flex;
  flex-direction: column;
  padding: 0;
}

.container-darks {
  position: relative;
  padding: 0.7rem 1.9rem;
  padding-bottom: 1.2rem;
  padding-top: 1.3rem;
  border-left: 3px solid var(--color-muted);
  background-color: var(--color-bg-alpha);
  margin: 1.5rem;
  margin-top: 2rem;
  border-radius: 3px 12px 12px 3px;
  box-shadow: 5px 4px 9px -4px var(--color-shadow-dark),
    -7px -6px 9px -4px var(--color-shadow-dark);
}
.ctrl-container {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 2.5rem;
  margin-right: 1.5rem;

  align-items: flex-end;
}

.add-button {
}
</style>
